
#content {
  margin: 0 auto;
  width: 75vw;
}
html {
    font-family: "Roboto", "Droid Sans", "Open Sans", sans-serif;
    font-weight: normal;
    color: rgba(0,0,0,0.87);
}
body{
  margin: 0;
}
a{
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  background-color: transparent;
}
img {
  border: 0;
}
*, *:before, *:after{
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}

@media only screen and (min-width: 601px) {
.navbar-fixed {
    height: 64px;
    line-height: 64px;
}
}
.navbar-fixed {
  position: relative;
  height: 56px;
  z-index: 997;
}
.navbar-fixed nav {
    position: fixed;
}
nav{
  color: #FFF;
  width: 100%;
  background-color: #27ae60;
  height: 56px;
  line-height: 56px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
          box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
  display: block;
}
.nav-wrapper{
  margin: 0 auto;
  display: block;
  width: 75vw;
}
.nav-wrapper ul{
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 100%;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding-left: 0;
  list-style-type: none;
  margin: 0;
}
.nav-wrapper ul li{
  list-style-type: none;
  -webkit-transition: background-color .3s;
  transition: background-color .3s;
  float: left;
  padding: 0;
}
nav ul a {
    -webkit-transition: background-color .3s;
    transition: background-color .3s;
    font-size: 1rem;
    color: #fff;
    display: block;
    padding: 0 15px;
    cursor: pointer;
}
nav ul a:hover {
    background-color: rgba(0,0,0,0.1);
}
.nav-wrapper ul a{
  font-size: 3vw;
}
@media (min-width: 600px) {
.nav-wrapper ul a{
    font-size: 18px; /* 600 * 3vw (600 * .03) */
}
}
@media (max-width: 405px) {
.nav-wrapper ul a{
    font-size: 2.4vw;
}
}

.home img{
  max-width: 100%;
  height: auto;
  width: auto;
  display: block;
  margin: 0 auto;
}
.image-container{
  position: relative; 
  width: 100%;
  display: block;
  margin: 0 auto;
}
.home h3{
  position: absolute; 
  bottom: 30%; 
  left: 0; 
  width: 100%; 
  text-align: center;
  background: white;
  padding: 11px;
  font-size: 3vw;
}
@media (min-width: 1400px) {
.home h3{
    font-size: 42px; /* 1400 * 3vw (1400 * .03) */
}
}






























































.gh-profile-container{
    background-color: #f2f2f2;
    border-radius: 0 0 6px 6px;
    border: .5px solid #e5e5e5;
    color: #333;
}
.gh-profile-container a{
  color: #333;
  padding:7px 0;
}
.avatar {
  margin: 0 auto;
  display: block;
  border-radius: 50%;
  max-width: 100%;
  height: auto;
  width: auto;
}
.gh-user-name{
  text-align: center;
}
.gh-links-container{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
}
.gh-link-container{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  border: .5px solid #9e9e9e;
  border-right: 0;
}
.gh-link-container:last-child{
  border-right: .5px solid #9e9e9e;
  border-bottom-right-radius: 6px;
}
.gh-link-container:first-child{
  border-bottom-left-radius: 6px;
}
a.gh-link-container:hover{
  background-color: #d1d1d1;
}

.repo-item {
    border-radius: 6px;
    border: .5px solid #e5e5e5;
    margin: 11px 0;
    display: block;
    text-decoration: none!important;
    overflow: hidden;
    color: #5c6877;
}
.repo-content {
    padding: 15px;
    background-color: #f2f2f2;
}
.repo-content .repo-title {
    font-size: 18px;
}
.repo-content .repo-desc {
    font-size: 14px;
    margin-top: 5px;
    word-wrap: break-word;
}
.text-holder-wrapper {
    display: block;
}
.repo-content .repo-info {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-pack: justify;
    -webkit-box-pack: justify;
            justify-content: space-between;
    margin-top: 15px;
}
.repo-content .repo-language {
    font-size: 14px;
    color: #77818e;
}
.repo-stats{
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.repo-stats p{
  margin: 0 15px;
  line-height: normal;
}
.repo-item svg{
  fill: #5c6877;
}
